// src/axiosInstance.js
import axios from 'axios';

// To mt axios instance vi cu hnh c bn
const axiosInstance = axios.create({
  baseURL: process.env.REACT_APP_API_URL, // URL c s ca API
  timeout: 10000, // Thi gian ch (timeout) 10 giy
  headers: {
    //'Content-Type': 'application/json'
    // 'Content-Type': 'multipart/form-data'
  },
});
console.log(process.env.REACT_APP_API_URL)
// Thm interceptor  x l cc yu cu
axiosInstance.interceptors.request.use(
  (config) => {
    // Thm token vo header nu c
    const token = localStorage.getItem('accessToken');
    if (token) {
      console.log(token)
      config.headers['Authorization'] = `Bearer ${token}`;
    }
    // Nu l yu cu POST hoc PUT vi kiu d liu multipart/form-data
    if (config.headers['Content-Type'] === 'multipart/form-data') {
      config.headers['Content-Type'] = 'multipart/form-data';
    } else {
      // t Content-Type cho cc yu cu khc
      config.headers['Content-Type'] = 'application/json';
    }
    return config;
    
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Thm interceptor  x l phn hi
axiosInstance.interceptors.response.use(
  (response) => {
    return response;
  },
  async (error) => {
    const originalRequest = error.config;
console.log(error)
    // Nu li l 401 (Unauthorized) v cha th li
    if (error.response && (error.response.status === 401) && !originalRequest._retry) {
      originalRequest._retry = true;
      try {
        // Gi yu cu refresh token
        console.log("test refresh: "+localStorage.getItem('refreshToken'))
        const response = await axios.post(process.env.REACT_APP_API_URL+'/users/refresh-token', {
        "refreshToken": localStorage.getItem('refreshToken'),
        });
        const { token } = response.data;
        console.log("test: "+token)

        // Lu token mi vo localStorage
        localStorage.setItem('accessToken', token);

        // Cp nht token cho yu cu gc
        axiosInstance.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        originalRequest.headers['Authorization'] = `Bearer ${token}`;

        // Thc hin li yu cu gc vi token mi
        return axiosInstance(originalRequest);
      } catch (refreshError) {
        // X l li khi refresh token khng thnh cng
        console.log('Refresh token failed', refreshError);
        // V d: iu hng n trang ng nhp
      }
    }
    return Promise.reject(error);
  }
);

export default axiosInstance;
