// src/features/auth/authSlice.js
import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import axios from 'axios';
import axiosInstance from '../utils/axiosInstance';

// Thay i URL v cu hnh ph hp vi API ca bn
const API_URL = process.env.REACT_APP_API_URL;

export const login = createAsyncThunk('auth/login', async ({ username, password }, thunkAPI) => {
  try {
    const response = await axios.post(`${API_URL}/users/login`, 
      {"phone_number": username, password },
      {
        headers: {
          'Content-Type': 'application/json' // m bo gi yu cu vi Content-Type l JSON
        }
     }
    );
    console.log(response.data)
    return response.data; // Tr v d liu t phn hi
  } catch (error) {
    console.log(error)
    return thunkAPI.rejectWithValue(error.response.data); // Tr v li nu c
  }
});



export const logout = createAsyncThunk('auth/logout', async (_, thunkAPI) => {
  // Xa token hoc thc hin cc thao tc logout khc
  try {
    // V d xa token khi localStorage
    
    return {}; // Tr v gi tr trng khi logout thnh cng
  } catch (error) {
    return thunkAPI.rejectWithValue(error.response.data); // Tr v li nu c
  }
});
const getFromLocalStorage = (key, defaultValue) => {
  const value = localStorage.getItem(key);
  return value ? JSON.parse(value) : defaultValue;
};

const authSlice = createSlice({
  name: 'auth',
  initialState: {
    user: getFromLocalStorage('user', null),
    token: null,
    refreshToken: null,
    status: 'idle',
    error: null,
  },
  reducers: {
    setUser(state, action) {
      state.user = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(login.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(login.fulfilled, (state, action) => {
        state.status = 'succeeded';
        state.token = action.payload.token;
        state.refreshToken=action.payload.refresh_token
        state.user = action.payload.username;
        // Lu token vo localStorage
        localStorage.setItem('accessToken', action.payload.token);
        localStorage.setItem('refreshToken', action.payload.refresh_token);
        localStorage.setItem('user', JSON.stringify(action.payload.username));
      })
      .addCase(login.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload.error; // Gi s li nm trong `error`
      })
      .addCase(logout.fulfilled, (state) => {
        state.status = 'idle';
        state.refreshToken = null;
        state.token = null;
        state.user = null;
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken');
        localStorage.removeItem('user');
      })
      .addCase(logout.rejected, (state, action) => {
        state.status = 'failed';
        state.error = action.payload.error; // Gi s li nm trong `error`
      })
   ;
  },
});

export const { setUser } = authSlice.actions;
export default authSlice.reducer;
